@import '../../assets/css/variable.less';

.governance-box {
  border-top: 1px solid @divider_color;

  .check {
    display: flex;
    margin-top: 18px;
    margin-left: 45px;

    div {
      width: 88px;
      height: 31px;
      background: rgba(85, 119, 253, 0.1);
      margin-left: 7px;
      border-radius: 4px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: @whiteFontColor;
      cursor: pointer;

      a {
        text-decoration: none;
        color: @whiteFontColor;
      }
    }

    .now {
      background: #5577FD;
    }
  }

  .now-prom {
    margin-top: 28px;
    margin-left: 50px;
  }

  .finished-box {
    width: 1140px;
    height: 790px;
    border: 1px solid @divider_color;
    margin: 0 auto;
    margin-top: 48px;
    font-weight: normal;

    background: linear-gradient(106.26deg, rgba(97, 122, 255, 0.1) 1.22%, rgba(223, 229, 255, 0) 51.54%, rgba(85, 91, 237, 0.05) 100.82%);
    .finished-title {
      margin-left: 32px;
      margin-top: 23px;

      .title-text {
        font-size: 32px;
        color: #FFFFFF;
      }

      .title-describe {
        margin-top: 16px;
        font-size: 14px;
      }
    }

    .finished-detail {
      margin-top: 24px;

      .finished-detail-box {
        width: 1138px;
        height: 136px;
        display: flex;
        border: 1px solid @divider_color;
        border-radius: 4px;
        margin-top: 24px;
        background: rgba(85, 119, 253, 0.1);
        .finished-detail-box-describe,
        .finished-detail-box-title {
          display: flex;
          justify-content: center;
          align-items: center;
          color: @whiteFontColor;

        }

        .finished-detail-box-title {
          width: 160px;
          border-right: 1px solid @divider_color;
          font-size: 32px;
          font-weight: 600;
        }

        .finished-detail-box-describe {
          width: 976px;
          font-size: 24px;

          span {
            width: 855px;
          }
        }
      }
    }
  }
}

@media only screen and (min-width : 1085px) and (max-width:1140px) {
  .governance-box {
    .finished-box {
      width: 1085px;

      .finished-detail {
        .finished-detail-box {
          width: 1083px;
          .finished-detail-box-title {
            width: 153px;
          }

          .finished-detail-box-describe {
            width: 933px;

          }
        }

      }

    }
  }
}
@media only screen and (min-width : 760px) and (max-width:1085px) {
  .governance-box {
    .finished-box {
      width: 711px;

      .finished-detail {
        .finished-detail-box {
          width: 709px;
          .finished-detail-box-title {
            width: 133px;
          }

          .finished-detail-box-describe {
            width: 579px;
            span{
              width: 558px;
            }
          }
        }

      }

    }
  }
}
@media only screen and (max-width : 760px) {
  .governance-box {
    .finished-box {
      width: 326px;
      border: 0;
      .finished-detail {
        .finished-detail-box {
          display: block;
          width: 324px;
          height: 338px;
          .finished-detail-box-title {
            width: 324px;
            height: 88px;
            border: 0;
            display: block;
            line-height: 88px;
            padding-left: 24px;
            border-bottom: 1px solid @divider_color;
          }

          .finished-detail-box-describe {
            width: 324px;
            display: block;
            padding: 0 24px;
            padding-top: 64px;

            span{
              width: 226px;
            }
          }
        }
        .dip-one{
          height: 401px;
        }
      }

    }
  }
}
